import { APIGatewayProxyEventV2, APIGatewayProxyResultV2, Context } from 'aws-lambda';
import { DynamoDBClient } from '@aws-sdk/client-dynamodb';
import { DynamoDBDocumentClient, PutCommand, GetCommand, QueryCommand, UpdateCommand, ScanCommand, DeleteCommand } from '@aws-sdk/lib-dynamodb';
import { v4 as uuidv4 } from 'uuid';

const dynamoClient = new DynamoDBClient({ region: process.env.REGION || 'us-east-2' });
const docClient = DynamoDBDocumentClient.from(dynamoClient);

const ADMIN_EMAIL = process.env.ADMIN_EMAIL || 'showchoirtreasurer@gmail.com';

const corsHeaders = {
  'Content-Type': 'application/json',
  'Access-Control-Allow-Origin': '*',
  'Access-Control-Allow-Headers': 'Content-Type,Authorization',
  'Access-Control-Allow-Methods': 'GET,POST,PUT,DELETE,OPTIONS',
};

interface Student {
  studentId: string;
  schoolYear: string;
  firstName: string;
  lastName: string;
  choir: string[];
  parentIds: string[];
  balance: number;
  createdAt: string;
  updatedAt: string;
}

interface Parent {
  parentId: string;
  firstName: string;
  lastName: string;
  email: string;
  phone?: string;
  studentIds: string[];
  createdAt: string;
}

interface Transaction {
  transactionId: string;
  date: string;
  studentId: string;
  schoolYear: string;
  description: string;
  type: 'charge' | 'credit';
  amount: number;
  notes?: string;
  createdAt: string;
  createdBy: string;
}

export const handler = async (
  event: APIGatewayProxyEventV2,
  context: Context
): Promise<APIGatewayProxyResultV2> => {
  console.log('Event:', JSON.stringify(event, null, 2));

  const path = event.rawPath;
  const method = event.requestContext.http.method;

  // Handle OPTIONS for CORS
  if (method === 'OPTIONS') {
    return {
      statusCode: 200,
      headers: corsHeaders,
      body: '',
    };
  }

  try {
    // Health check
    if (path === '/health' && method === 'GET') {
      return {
        statusCode: 200,
        headers: corsHeaders,
        body: JSON.stringify({
          status: 'healthy',
          timestamp: new Date().toISOString(),
          region: process.env.REGION || 'us-east-2',
          tables: {
            students: process.env.STUDENTS_TABLE,
            parents: process.env.PARENTS_TABLE,
            transactions: process.env.TRANSACTIONS_TABLE,
          }
        }),
      };
    }

    // Admin check
    if (path === '/admin/check' && method === 'POST') {
      const body = JSON.parse(event.body || '{}');
      const userEmail = body.email || '';
      const isAdmin = userEmail.toLowerCase() === ADMIN_EMAIL.toLowerCase();
      
      return {
        statusCode: 200,
        headers: corsHeaders,
        body: JSON.stringify({
          isAdmin,
          userEmail,
          adminEmail: ADMIN_EMAIL,
        }),
      };
    }

    // Students endpoints
    if (path === '/students' || path.startsWith('/students/')) {
      if (method === 'GET' && path === '/students') {
        const params = event.queryStringParameters;
        const schoolYear = params?.schoolYear;
        
        if (!process.env.STUDENTS_TABLE) {
          return {
            statusCode: 200,
            headers: corsHeaders,
            body: JSON.stringify([]),
          };
        }

        try {
          const result = await docClient.send(new ScanCommand({
            TableName: process.env.STUDENTS_TABLE,
          }));
          
          let items = result.Items || [];
          if (schoolYear) {
            items = items.filter((item: any) => item.schoolYear === schoolYear);
          }
          
          return {
            statusCode: 200,
            headers: corsHeaders,
            body: JSON.stringify(items),
          };
        } catch (error) {
          console.error('Error fetching students:', error);
          return {
            statusCode: 200,
            headers: corsHeaders,
            body: JSON.stringify([]),
          };
        }
      }

      if (method === 'POST' && path === '/students') {
        const body = JSON.parse(event.body || '{}');
        const student: Student = {
          studentId: uuidv4(),
          schoolYear: body.schoolYear,
          firstName: body.firstName,
          lastName: body.lastName,
          choir: body.choir || [],
          parentIds: body.parentIds || [],
          balance: 0,
          createdAt: new Date().toISOString(),
          updatedAt: new Date().toISOString(),
        };

        if (process.env.STUDENTS_TABLE) {
          await docClient.send(new PutCommand({
            TableName: process.env.STUDENTS_TABLE,
            Item: student,
          }));
        }

        return {
          statusCode: 201,
          headers: corsHeaders,
          body: JSON.stringify(student),
        };
      }

      // Update student (for editing choirs, etc.)
      if (method === 'PUT' && path.startsWith('/students/')) {
        const studentId = path.split('/')[2];
        const body = JSON.parse(event.body || '{}');
        
        if (process.env.STUDENTS_TABLE) {
          // Get existing student
          const getResult = await docClient.send(new GetCommand({
            TableName: process.env.STUDENTS_TABLE,
            Key: { studentId },
          }));
          
          if (getResult.Item) {
            const updatedStudent = {
              ...getResult.Item,
              ...body,
              studentId, // Ensure ID doesn't change
              updatedAt: new Date().toISOString(),
            };
            
            await docClient.send(new PutCommand({
              TableName: process.env.STUDENTS_TABLE,
              Item: updatedStudent,
            }));
            
            return {
              statusCode: 200,
              headers: corsHeaders,
              body: JSON.stringify(updatedStudent),
            };
          }
        }
        
        return {
          statusCode: 404,
          headers: corsHeaders,
          body: JSON.stringify({ error: 'Student not found' }),
        };
      }
    }

    // Parents endpoints
    if (path === '/parents' || path.startsWith('/parents/')) {
      if (method === 'GET' && path === '/parents') {
        if (!process.env.PARENTS_TABLE) {
          return {
            statusCode: 200,
            headers: corsHeaders,
            body: JSON.stringify([]),
          };
        }

        try {
          const result = await docClient.send(new ScanCommand({
            TableName: process.env.PARENTS_TABLE,
          }));
          
          return {
            statusCode: 200,
            headers: corsHeaders,
            body: JSON.stringify(result.Items || []),
          };
        } catch (error) {
          console.error('Error fetching parents:', error);
          return {
            statusCode: 200,
            headers: corsHeaders,
            body: JSON.stringify([]),
          };
        }
      }

      if (method === 'POST' && path === '/parents') {
        const body = JSON.parse(event.body || '{}');
        const parent: Parent = {
          parentId: uuidv4(),
          firstName: body.firstName,
          lastName: body.lastName,
          email: body.email,
          phone: body.phone,
          studentIds: body.studentIds || [],
          createdAt: new Date().toISOString(),
        };

        if (process.env.PARENTS_TABLE) {
          await docClient.send(new PutCommand({
            TableName: process.env.PARENTS_TABLE,
            Item: parent,
          }));
        }

        return {
          statusCode: 201,
          headers: corsHeaders,
          body: JSON.stringify(parent),
        };
      }
    }

    // Transactions endpoints
    if (path === '/transactions' || path.startsWith('/transactions/')) {
      if (method === 'GET' && path === '/transactions') {
        const params = event.queryStringParameters;
        const schoolYear = params?.schoolYear;
        
        if (!process.env.TRANSACTIONS_TABLE) {
          return {
            statusCode: 200,
            headers: corsHeaders,
            body: JSON.stringify([]),
          };
        }

        try {
          const result = await docClient.send(new ScanCommand({
            TableName: process.env.TRANSACTIONS_TABLE,
          }));
          
          let items = result.Items || [];
          if (schoolYear) {
            items = items.filter((item: any) => item.schoolYear === schoolYear);
          }
          
          return {
            statusCode: 200,
            headers: corsHeaders,
            body: JSON.stringify(items),
          };
        } catch (error) {
          console.error('Error fetching transactions:', error);
          return {
            statusCode: 200,
            headers: corsHeaders,
            body: JSON.stringify([]),
          };
        }
      }

      if (method === 'POST' && path === '/transactions') {
        const body = JSON.parse(event.body || '{}');
        const transaction: Transaction = {
          transactionId: uuidv4(),
          date: body.date || new Date().toISOString().split('T')[0],
          studentId: body.studentId,
          schoolYear: body.schoolYear,
          description: body.description,
          type: body.type,
          amount: body.amount,
          notes: body.notes,
          createdAt: new Date().toISOString(),
          createdBy: 'admin',
        };

        if (process.env.TRANSACTIONS_TABLE) {
          await docClient.send(new PutCommand({
            TableName: process.env.TRANSACTIONS_TABLE,
            Item: transaction,
          }));
          
          // Update student balance
          if (process.env.STUDENTS_TABLE && transaction.studentId) {
            try {
              const studentResult = await docClient.send(new GetCommand({
                TableName: process.env.STUDENTS_TABLE,
                Key: { studentId: transaction.studentId },
              }));
              
              if (studentResult.Item) {
                const currentBalance = studentResult.Item.balance || 0;
                const balanceChange = transaction.type === 'charge' ? transaction.amount : -transaction.amount;
                const newBalance = currentBalance + balanceChange;
                
                await docClient.send(new UpdateCommand({
                  TableName: process.env.STUDENTS_TABLE,
                  Key: { studentId: transaction.studentId },
                  UpdateExpression: 'SET balance = :balance, updatedAt = :now',
                  ExpressionAttributeValues: {
                    ':balance': newBalance,
                    ':now': new Date().toISOString(),
                  },
                }));
              }
            } catch (error) {
              console.error('Error updating student balance:', error);
            }
          }
        }

        return {
          statusCode: 201,
          headers: corsHeaders,
          body: JSON.stringify(transaction),
        };
      }
    }

    // Invoice endpoints
    if (path === '/invoices/send' && method === 'POST') {
      const body = JSON.parse(event.body || '{}');
      
      return {
        statusCode: 200,
        headers: corsHeaders,
        body: JSON.stringify({
          message: 'Invoice would be sent here',
          studentId: body.studentId,
          schoolYear: body.schoolYear,
        }),
      };
    }

    // Default response
    return {
      statusCode: 404,
      headers: corsHeaders,
      body: JSON.stringify({
        message: 'Not Found',
        path,
        method,
      }),
    };

  } catch (error) {
    console.error('Error:', error);
    return {
      statusCode: 500,
      headers: corsHeaders,
      body: JSON.stringify({
        error: 'Internal Server Error',
        details: error instanceof Error ? error.message : 'Unknown error',
      }),
    };
  }
};
